.unit {
  position: relative;
  margin: 25px 0;

  &::after {
    content: "";
    display: block;
    width: 21px;
    background-color: #fff;
    position: absolute;
    left: -36px;
    z-index: 1;
    top: -2px;
    height: 30px;
  }

  &::before {
    content: "";
    display: block;
    width: 16px;
    height: 16px;
    background-color: #e9e4e4;
    border-radius: 50%;
    position: absolute;
    left: -32px;
    z-index: 2;
    top: 5px;
  }
}

.button {
  &:hover {
    .buttonText {
      border: none;
    }
  }
}

.buttonText {
  font-size: 18px;
  color: #4a4a4a;
  border-bottom: 1px dashed #4a4a4a;
}
