.lesson {
  position: relative;
  margin-left: 23px;
  color: #4a4a4a;
  line-height: 25px;
  font-size: 14px;

  &::before {
    content: "";
    display: block;
    width: 12px;
    height: 12px;
    background-color: #e9e4e4;
    border-radius: 50%;
    position: absolute;
    left: -18px;
    top: 8px;
  }
}
