.units {
  position: relative;
  margin-left: 30px;
  padding: 1px 0 1px 44px;

  &::after {
    content: "";
    display: block;
    height: 100%;
    width: 1px;
    background-color: #d4d4d4;
    position: absolute;
    top: 0px;
    left: 20px;
    margin: 5px 0;
  }
}

.lessons {
  padding-top: 5px;
}
