.level {
  margin-top: 12px;
}

.arrow {
  height: auto;

  &__open {
    transform: rotate(90deg);
  }
}

.button {
  display: flex;
  align-items: center;

  &:hover {
    text-decoration: underline;
    color: #494949;
  }
}

.buttonText {
  margin-left: 18px;
  font-size: 22px;
  font-weight: 700;
  color: #494949;
}
